@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro');

#tsparticles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  pointer-events: none;


}

.card {
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  pointer-events: none;
  z-index: 1;
  width: clamp(300px, 50vw, 600px);
  padding: clamp(1rem, 3vw, 5rem);
}

.ssp {
  color: #000000;
  font-family: 'Source Sans Pro', sans-serif;
  background: rgba(255, 255, 255, 0);
  text-align: center;
  user-select: none;
  margin: 0.5rem;
  z-index: 1;
}

.ssp.upper {
  font-size: clamp(2.25rem, 6vw, 4rem);
}

.ssp.lower {
  font-size: clamp(1rem, 3vw, 2.25rem);
}

.centerimage {
  height: auto;
  z-index: 1;
  width: clamp(180px, 25vw, 400px);
  margin: clamp(5px, 12.5px, 20px)
}

.iconContainer {
  position: relative;
  margin-top: 2rem;
  width: 50%;
  min-width: 175px;
  height: 4rem;
  margin-left: auto;
  margin-right: auto;
  z-index: 1;
  pointer-events: auto;
}

.icon {
  position: absolute;
  top: 0;
}

.icon:hover {
  color: grey;
}

.icon.left {
  left: 0;
}

.icon.right {
  right: 0;
}